<template>
	<transition name='mainBody'>
		<div class="content">
			<div class="wrapper-out" >
				<ul class="clearfix wrapperUl" id="wrapperUl">
					<li v-for="(item,index) in bannerImg">
						<a href="javascript:void(0)"><img :src="item.img"></a>
					</li>
				</ul>
				<div class="bannerBtn">
					<i class="active"></i>
					<i></i>
					<i></i>
				</div>
			</div>
			<home-show></home-show>
		</div>
	</transition>
</template>

<script>
import '../assets/js/banner.js'
import homeShow from '../view/homeShow'
	export default{
		data(){
			return {
				bannerImg:[{img:require('../assets/img/diao.jpg'),
							address:'http://144860797.pc.cms.ilanhai.net:81/'},
							{img:require('../assets/img/yu.jpg'),
							address:'http://126617450.pc.cms.ilanhai.net:81/'},
							{img:require('../assets/img/shan.jpg'),
							address:'http://407644121.pc.cms.ilanhai.net:81/'}],
				autoBanner:'',
				autoIndex:0
			}
		},
		methods:{
			
		},
		components:{
			homeShow
		}
	}
</script>


